body {
  padding: 0;
  margin: 0;
  border: 0;
  font-family: 'Josefin Sans', sans-serif;
  background-color: var(--white);
}

/*Colors*/
:root {
  --light-gray: #a7aabc;
  --dark-gray: #434656;
  --main-blue: #5a6eab;
  --light-blue: #e8fdff;
  --coral: #e48b94;
  --available: #219284;
  --unavailable: #E48B94;
}

/*fonts*/
p {
  color: var(--dark-gray);
  font-family: 'Josefin Sans', sans-serif;
  font-size: 12px;
  margin-bottom: 2px;
  margin-top: 10px;
}

h1 {
  color: var(--main-blue);
  font-family: 'Figtree', sans-serif;
  font-size: 40px;
}

h2 {
  color: var(--main-blue);
  font-family: 'Figtree', sans-serif;
  font-size: 30px;
}

h3 {
  color: var(--main-blue);
  font-family: 'Josefin Sans', sans-serif;
  font-size: 20px;
}

.header-title {
  margin-left: 5%;
}

button {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color: var(--main-blue);
  font-family: 'Josefin Sans', sans-serif;
  font-weight: bold;
  border-radius: 10px;
  color: var(--white);
  font-size: 10px;
  border: none;
  margin: 2px;
}

button:hover {
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 3px;
  padding-bottom: 3px;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: bold;
  background-color: var(--white);
  border: 2px solid var(--main-blue);
  color: var(--dark-gray);
  font-size: 10px;
  cursor: pointer;
  border-radius: 10px;
  margin: 2px;
}

@media screen and (min-width: 600px) {

  /*This is designed for only mobile, so we use 400px as a max width*/
  .navlink-profile {
   left: 28%;
  }

  body {
    width: 400px;
    margin-left: auto;
    margin-right: auto;
  }

  nav {
    width: 400px;
    margin-left: auto;
    margin-right: auto;
    position: fixed;
    bottom: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    font-family: 'Josefin Sans', sans-serif;
    background-color: var(--white);
    text-decoration: none;
    font-size: 10px;
  }
}


@media screen and (max-width: 600px) {

  .all-nav {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }


  nav {
    position: fixed;
    width: 100%;
    height: 10%;
    bottom: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    font-family: 'Josefin Sans', sans-serif;
    background-color: var(--main-blue);
    border-radius: 30px 30px 0px 0px;
    text-decoration: none;
    font-size: 10px;
  }

  body {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }

}

/*Navigation*/


.navlink {
  text-transform: uppercase;
  font-weight: bold;
  padding: 10px;
  text-decoration: none;
  font-size: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}

.navlink active {
  background-color: white;

}

.navlink-profile {
  position: absolute;
  top: 2%;
  right: 5%;
  width: 50px;
  height: 5 0px;
}

.nav-icon {
  width: 30px;
  height: 30px;
  color: white;
}

/*Balance*/
.balance {
  border-radius: 5px;
  display: flex;
  align-items: center;
  padding: 10px;
  justify-content: space-around;
  width: 90%;
}

